* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'GFS Didot', serif;
  background-image: url('http://source.unsplash.com/yqBvJJ8jGBQ');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column nowrap;
  margin: 0 auto;
}

button {
  font-family: inherit;
  font-size: inherit;
  border: none;
  border-radius: 3px;
  box-shadow: 0px 0px 8px black;
  cursor: pointer;
}

.startStop {
  height: 5rem;
  width: 6rem;
}
.startStop:hover {
  background-color: rgb(167, 165, 165);
}

.difficulty__btn--up,
.difficulty__btn--down,
.difficulty__btn--ok {
  display: inline-block;
  text-align: center;
  height: 4rem;
  width: 4rem;
  margin: 1rem;
  background-color: transparent;
  border-radius: 50%;
  font-size: 3rem;
  transition: 200ms;
}

.difficulty__btn--up:hover,
.difficulty__btn--down:hover,
.difficulty__btn--ok:hover {
  filter: hue-rotate(-20deg);
  transform: scale(1.1);
}

.difficulty__btn--down {
  background-color: rgb(49, 190, 255);
}
.difficulty__btn--up {
  background-color: palevioletred;
}
.difficulty__btn--ok {
  background-color: rgb(29, 161, 29);
}

.container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-flow: column nowrap;
  min-height: 80vh;
  min-width: 70vw;
  /* make linear maybe*/
  background-image: linear-gradient(
    180deg,
    rgba(117, 177, 223, 0.673),
    rgba(73, 91, 191, 0.52),
    rgba(125, 82, 162, 0.534),
    rgba(143, 74, 45, 0.714)
  );
  margin-top: 4rem;
  border-radius: 5px;
  box-shadow: 0px 0px 20px black;
  border: 4px solid rgb(215, 174, 40);
}

/* HEADING
*************/

.speed__heading {
  color: yellow;
  font-size: 4rem;
  text-shadow: 0px 6px 10px aliceblue;
}

/* GAME BUTTONS
************/

.speed__buttons {
  display: flex;
  justify-content: space-around;
  flex-flow: row wrap;
}
.speed__button.active {
  background-image: url('./Assets/img/zeus.jpg');
  background-size: cover;
  filter: saturate(4);
  box-shadow: 0px 0px 20px yellow;
}

.speed__button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1rem;
  width: 7rem;
  height: 7rem;
  padding: 3rem;
  background-color: rgba(117, 122, 126, 0.502);
  border-radius: 50%;
  border: outset 3px rgba(0, 0, 0, 0.44);
  box-shadow: 0px 0px 7px black;
  cursor: pointer;
  /* change this */
  pointer-events: auto;
  transition: 100ms;
}

.speed__button:hover {
  border: inset 1px white;
}

.speed__button:first-child {
  background-color: #ccccff;
}

.speed__button:nth-child(2) {
  background-color: #6495ed;
}

.speed__button:nth-child(3) {
  background-color: #40e0d0;
}

.speed__button:nth-child(4) {
  background-color: #9fe2bf;
}
.speed__button:nth-child(5) {
  background-color: #ffbf00;
}
.speed__button:nth-child(6) {
  background-color: #ff7f50;
}
.speed__button:nth-child(7) {
  background-color: #de3163;
}
.speed__button:nth-child(8) {
  background-color: #9b59b6;
}

/* CURRENT SCORE
****************/
.speed__score {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column nowrap;
  color: aliceblue;
}
h3 {
  color: yellow;
}

/* CONTROLS
*************/

.speed__controls button {
  margin: 1rem;
}

#start {
}

#stop {
  display: flex;
}

/* Overlay
**************/

#overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  background-color: white;
  padding: 2rem;
  width: 40%;
  height: 25%;
  border-radius: 10px;
  position: relative;
}

/* fix button */
.modal button {
  width: 4rem;
  height: 2rem;
  padding: 5px;
  position: absolute;
  top: 0;
  right: 0;
  background-color: transparent;
  box-shadow: none;
}

#result {
  font-size: 1.5rem;
}

@media (max-width: 600px) {
  .speed__buttons {
    display: flex;
    justify-content: space-around;
    flex-flow: row wrap;
  }
  .modal {
    background-color: white;
    padding: 2rem;
    width: 80%;
    height: 40%;
    border-radius: 10px;
    position: relative;
  }
}
